	module.exports = {"data":{"wordpressPage":{"title":"Managing postabortion hemorrhage","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Hemorrhage caused by atony may be treated with uterine massage, uterotonic medications, re-aspiration, tamponade or surgery. Women need to be closely monitored for symptoms of shock.</li>\n</ul>\n<h2>Strength of recommendation: Strong</h2>\n<h3>Quality of evidence: Low</h3>\n<h3>Epidemiology</h3>\n<p>Definitions of postabortion hemorrhage vary, making comparisons of incidence, risk factors and treatments across studies difficult. A definition proposed by the Society for Family Planning is: excessive bleeding that requires a clinical response such as transfusion or hospital admission, and/or bleeding in excess of 500mL (Kerns &amp; Steinauer, 2013). Hemorrhage after induced abortion is rare, occurring in 0-3 per 1,000 cases following medical abortion up to 9 weeks gestation or vacuum aspiration before 13 weeks gestation, and 0.9-10 per 1,000 cases following  uterine evacuation at or after 13 weeks gestation (Kerns &amp; Steinauer, 2013; Upadhyay et al., 2014). Causes of bleeding include placenta previa or accreta, uterine atony, retained products of conception, cervical or vaginal laceration, uterine injury, and coagulopathy (Kerns &amp; Steinauer, 2013; Perriera, Arslan, &amp; Masch, 2017).</p>\n<h3>Diagnosis</h3>\n<p>When postabortion hemorrhage is suspected, clinicians should take a rapid, systematic approach to assessing and treating women. Initial assessment includes inspection of the cervix for laceration, bimanual examination to assess for uterine atony and tenderness, and uterine aspiration or ultrasound examination to evaluate for retained products of conception or blood.</p>\n<h3>Management</h3>\n<p>Cervical lacerations may be treated with direct pressure with gauze or ring forceps, application of topical clotting agents (silver nitrate or ferric subsulfate solution), or by placing absorbable sutures.</p>\n<p>Uterine atony requires a rapid, sequential response starting with uterine massage, followed by uterotonics, re-aspiration, uterine tamponade and finally surgical measures. Clinicians should move quickly to the next step if bleeding is not controlled. When uterotonic medications are used, additional or repeat doses may be used if bleeding does not improve after the first dose.</p>\n<p>Uterotonic medications and dosages:*</p>\n<table>\n<thead>\n<tr>\n<td>Medication</td>\n<td>Dosage</td>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Methylergonovine</td>\n<td>0.2mg intramuscularly or intracervically; can be repeated every 2-4 hours.  Avoid in women with hypertension.</td>\n</tr>\n<tr>\n<td>Misoprostol</td>\n<td>800mcg sublingually or rectally</td>\n</tr>\n<tr>\n<td>Oxytocin</td>\n<td>10-40 units per 500-1000mL fluid intravenously or 10 units intramuscularly</td>\n</tr>\n<tr>\n<td>Intrauterine tamponade</td>\n<td>Sterile gauze, 30-75mL Foley catheter balloon, condom catheter or obstetric balloon placed in uterus</td>\n</tr>\n</tbody>\n</table>\n<p>*Extrapolated from postpartum data (American College of Obstetricians and Gynecologists, 2017; Kerns &amp; Steinauer, 2013; Mavrides et al., 2016; Morris et al., 2017; Prata &amp; Weidert, 2016; World Health Organization, 2012).</p>\n<p>If tamponade is used to stop bleeding, the Foley balloon, obstetric balloon, gauze or inflated condom catheter should be left in place for several hours while the patient is observed. If the woman remains stable after the balloon or gauze is removed, she may be discharged.</p>\n<p>When bleeding continues after assurance of complete uterine evacuation and no visible lacerations, providers must consider other complications, such as perforation, coagulopathy or placenta accreta (National Abortion Federation, 2017). If coagulopathy, such as disseminated intravascular coagulation, is present, blood products may be required. Surgical measures including hysterectomy, uterine compression sutures, uterine artery ligation or uterine artery embolization can be performed for severe bleeding that cannot be controlled by other measures. Providers at health centers without available operating theaters or expertise should have clear protocols for resuscitation and transfer to a higher level of care. Women at risk of shock require intravenous line placement, supplemental oxygen, fluid resuscitation and replacement of blood products as indicated.</p>\n<h3>References</h3>\n<p>American College of Obstetricians and Gynecologists. (2017). ACOG Practice Bulletin Number 183: Postpartum hemorrhage. <em>Obstetrics &amp; Gynecology, 130</em>(4), e168-e186.</p>\n<p>Kerns, J. &amp; Steinauer, J. (2013). Society of Family Planning Clinical Guideline 20131: Management of postabortion hemorrhage. <em>Contraception</em>, <em>87</em>(3), 331-42.</p>\n<p>Mavrides, E., Allard, S., Chandraharan, E., Collins, P., Green, L., Hunt, B. J., … on behalf of the Royal College of Obstetricians and Gynaecologists. (2016). Prevention and management of postpartum hemorrhage. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology 124,</em> e106-e149.</p>\n<p>Morris, J. L., Winikoff, B., Dabash, R., Weeks, A., Faundes, A., Gemzell-Danielsson, … Visser, G. H. A. (2017). FIGO’s updated recommendations for misoprostol used alone in gynecology and obstetrics. <em>International Journal of Gynecology &amp; Obstetrics, 138</em>(3), 363-366.</p>\n<p>National Abortion Federation. (2017). <em>Clinical Policy Guidelines</em>. Washington DC: National Abortion Federation.</p>\n<p>Perriera, L. K., Arslan, A. A., &amp; Masch, R. (2017). Placenta previa and the risk of adverse outcomes during second trimester abortion: A retrospective cohort study. <em>Australian and New Zealand Journal of Obstetrics and Gynaecology, 57</em>(1), 99-104.</p>\n<p>Prata, N., &amp; Weidert, K. (2016). Efficacy of misoprostol for the treatment of postpartum hemorrhage: Current knowledge and implications for health care planning. <em>International Journal of Women’s Health, 8</em>, 341-349.</p>\n<p>Upadhyay, U., Desai, S., Zlidar, V., Weitz, T. A., Grossman, D., Anderson, P., &amp; Taylor, D. (2014). Incidence of emergency department visits and complications after abortion. <em>Obstetrics &amp; Gynecology, 125</em>, 175-183.</p>\n<p>World Health Organization. (2012). <em>WHO recommendations for the prevention and treatment of postpartum hemorrhage</em>. Geneva: World Health Organization Press.</p>\n","fields":{"path":"postabortion-care/managing-hemorrhage"},"acf":{"last_reviewed":"20171116","landing_page":null}},"parent":{"title":"Postabortion care","fields":{"path":"postabortion-care"},"wordpress_parent":159},"grandparent":null,"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"01c96dda-8a1d-507f-8c69-160758a833a8","title":"Managing postabortion hemorrhage","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Hemorrhage caused by atony may be treated with uterine massage, uterotonic medications, re-aspiration, tamponade or surgery. Women need to be closely monitored for symptoms of shock.</li>\n</ul>\n<h2>Strength of recommendation: Strong</h2>\n<h3>Quality of evidence: Low</h3>\n<h3>Epidemiology</h3>\n<p>Definitions of postabortion hemorrhage vary, making comparisons of incidence, risk factors and treatments across studies difficult. A definition proposed by the Society for Family Planning is: excessive bleeding that requires a clinical response such as transfusion or hospital admission, and/or bleeding in excess of 500mL (Kerns &amp; Steinauer, 2013). Hemorrhage after induced abortion is rare, occurring in 0-3 per 1,000 cases following medical abortion up to 9 weeks gestation or vacuum aspiration before 13 weeks gestation, and 0.9-10 per 1,000 cases following  uterine evacuation at or after 13 weeks gestation (Kerns &amp; Steinauer, 2013; Upadhyay et al., 2014). Causes of bleeding include placenta previa or accreta, uterine atony, retained products of conception, cervical or vaginal laceration, uterine injury, and coagulopathy (Kerns &amp; Steinauer, 2013; Perriera, Arslan, &amp; Masch, 2017).</p>\n<h3>Diagnosis</h3>\n<p>When postabortion hemorrhage is suspected, clinicians should take a rapid, systematic approach to assessing and treating women. Initial assessment includes inspection of the cervix for laceration, bimanual examination to assess for uterine atony and tenderness, and uterine aspiration or ultrasound examination to evaluate for retained products of conception or blood.</p>\n<h3>Management</h3>\n<p>Cervical lacerations may be treated with direct pressure with gauze or ring forceps, application of topical clotting agents (silver nitrate or ferric subsulfate solution), or by placing absorbable sutures.</p>\n<p>Uterine atony requires a rapid, sequential response starting with uterine massage, followed by uterotonics, re-aspiration, uterine tamponade and finally surgical measures. Clinicians should move quickly to the next step if bleeding is not controlled. When uterotonic medications are used, additional or repeat doses may be used if bleeding does not improve after the first dose.</p>\n<p>Uterotonic medications and dosages:*</p>\n<table>\n<thead>\n<tr>\n<td>Medication</td>\n<td>Dosage</td>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Methylergonovine</td>\n<td>0.2mg intramuscularly or intracervically; can be repeated every 2-4 hours.  Avoid in women with hypertension.</td>\n</tr>\n<tr>\n<td>Misoprostol</td>\n<td>800mcg sublingually or rectally</td>\n</tr>\n<tr>\n<td>Oxytocin</td>\n<td>10-40 units per 500-1000mL fluid intravenously or 10 units intramuscularly</td>\n</tr>\n<tr>\n<td>Intrauterine tamponade</td>\n<td>Sterile gauze, 30-75mL Foley catheter balloon, condom catheter or obstetric balloon placed in uterus</td>\n</tr>\n</tbody>\n</table>\n<p>*Extrapolated from postpartum data (American College of Obstetricians and Gynecologists, 2017; Kerns &amp; Steinauer, 2013; Mavrides et al., 2016; Morris et al., 2017; Prata &amp; Weidert, 2016; World Health Organization, 2012).</p>\n<p>If tamponade is used to stop bleeding, the Foley balloon, obstetric balloon, gauze or inflated condom catheter should be left in place for several hours while the patient is observed. If the woman remains stable after the balloon or gauze is removed, she may be discharged.</p>\n<p>When bleeding continues after assurance of complete uterine evacuation and no visible lacerations, providers must consider other complications, such as perforation, coagulopathy or placenta accreta (National Abortion Federation, 2017). If coagulopathy, such as disseminated intravascular coagulation, is present, blood products may be required. Surgical measures including hysterectomy, uterine compression sutures, uterine artery ligation or uterine artery embolization can be performed for severe bleeding that cannot be controlled by other measures. Providers at health centers without available operating theaters or expertise should have clear protocols for resuscitation and transfer to a higher level of care. Women at risk of shock require intravenous line placement, supplemental oxygen, fluid resuscitation and replacement of blood products as indicated.</p>\n<h3>References</h3>\n<p>American College of Obstetricians and Gynecologists. (2017). ACOG Practice Bulletin Number 183: Postpartum hemorrhage. <em>Obstetrics &amp; Gynecology, 130</em>(4), e168-e186.</p>\n<p>Kerns, J. &amp; Steinauer, J. (2013). Society of Family Planning Clinical Guideline 20131: Management of postabortion hemorrhage. <em>Contraception</em>, <em>87</em>(3), 331-42.</p>\n<p>Mavrides, E., Allard, S., Chandraharan, E., Collins, P., Green, L., Hunt, B. J., … on behalf of the Royal College of Obstetricians and Gynaecologists. (2016). Prevention and management of postpartum hemorrhage. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology 124,</em> e106-e149.</p>\n<p>Morris, J. L., Winikoff, B., Dabash, R., Weeks, A., Faundes, A., Gemzell-Danielsson, … Visser, G. H. A. (2017). FIGO’s updated recommendations for misoprostol used alone in gynecology and obstetrics. <em>International Journal of Gynecology &amp; Obstetrics, 138</em>(3), 363-366.</p>\n<p>National Abortion Federation. (2017). <em>Clinical Policy Guidelines</em>. Washington DC: National Abortion Federation.</p>\n<p>Perriera, L. K., Arslan, A. A., &amp; Masch, R. (2017). Placenta previa and the risk of adverse outcomes during second trimester abortion: A retrospective cohort study. <em>Australian and New Zealand Journal of Obstetrics and Gynaecology, 57</em>(1), 99-104.</p>\n<p>Prata, N., &amp; Weidert, K. (2016). Efficacy of misoprostol for the treatment of postpartum hemorrhage: Current knowledge and implications for health care planning. <em>International Journal of Women’s Health, 8</em>, 341-349.</p>\n<p>Upadhyay, U., Desai, S., Zlidar, V., Weitz, T. A., Grossman, D., Anderson, P., &amp; Taylor, D. (2014). Incidence of emergency department visits and complications after abortion. <em>Obstetrics &amp; Gynecology, 125</em>, 175-183.</p>\n<p>World Health Organization. (2012). <em>WHO recommendations for the prevention and treatment of postpartum hemorrhage</em>. Geneva: World Health Organization Press.</p>\n","last_reviewed":"20171116","parent":166,"grandparent":null}}

